import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/styles';
import Typography from '@material-ui/core/Typography';
import MuiLink from '@material-ui/core/Link';
import { Link } from 'gatsby';
import FavoriteIcon from '@material-ui/icons/Favorite';
import red from '@material-ui/core/colors';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(4)
  },
  icon: {
    fontSize: 14,
    color: "#ea4335"
  }
}));

const Footer = props => {
  const { className, ...rest } = props;

  const classes = useStyles();

  return (
    <div
      {...rest}
      className={clsx(classes.root, className)}
    >
      <Typography variant="body2" color="textSecondary">
        <Link color="inherit" to="/license" >Copyright © Bipul Raman {new Date().getFullYear()}</Link>{' | '}    
        <Link to="/privacy">Privacy Policy</Link>{' | '}<Link to="/disclaimer">Discliamer</Link>
        {" | Designed & Developed with "}<FavoriteIcon className={classes.icon}  />{" in India  "}<img src="/images/India.svg" height="11px"></img>{" by Bipul."}
      </Typography>
    </div>
  );
};

Footer.propTypes = {
  className: PropTypes.string
};

export default Footer;
